/* App.css */
@font-face{
  font-family: "Hanna";
  src: url("fonts/BMDOHYEON_ttf.ttf");
}

body {
  font-family: "Hanna";
  margin: 0;
  padding: 0;
  background-color: rgb(245, 245, 245);
}

.container {
  width: 80%;
  margin: 0 auto;
}

header {
  font-family: "Hanna";
  background-color: #333;
  color: #fff;
  padding: 10px;
  text-align: center;
  border-bottom: 3px solid rgb(255, 216, 145) ;
}

.app-header {
  background-color: rgb(245, 245, 245);
  color: #333;
  padding: 10px;
  text-align: center;
}

.navbar {
  font-family: "Hanna";
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.navbar a {
  text-decoration: none;
  color: #333;
  font-weight: bold;
  padding: 10px;
  border-bottom: 2px solid transparent;
  transition: border-bottom 0.3s ease;
}

.navbar a:hover {
  border-bottom: 2px solid #333;
}

footer {
  font-family: "Hanna";
  background-color: rgb(245, 245, 245);
  border-top: 3px solid rgb(255, 216, 145) ;
  color: #818181;
  text-align: center;
  padding: 15px;
  position: fixed;
  bottom: 0;
  width: 100%;
  font-weight: bold;
  font-size: 0.8em;
}

/* index.css */
#index {
  font-family: "Hanna";
  display: flex;
  padding: 0px;
}

.index-text {
  flex: 1;
}

.index-name {
  font-size: 5.0em;
  font-weight: bold;
  margin-left: 0px; /* 좀 더 왼쪽으로 이동 */
}

.index-role {
  font-size: 2.5em;
  font-weight: bold;
  margin-top: -10px;
  margin-left: 15px;
  border: none;
  color: rgb(255, 216, 145);
}

.index-contact-info {
  margin-top: 40px;
  font-size: 1.2em;
  border: none;
  margin-left: 15px;
}

.index-contact-info strong {
  font-weight: bold;
}

.index-img,
.introduce-img {
  max-width: 48%;
  height: auto;
  border-radius: 10%; /* 50%로 설정하여 이미지를 둥글게 만듭니다 */
  margin-left: 10px;
}

#introduce {
  font-family: "Hanna";
  display: flex;
  padding: 0px;
}

.introduce-container {
  font-family: "Hanna";
  display: flex;
  align-items: center;
  max-width: 1000px;
  margin: 0 auto;
}

.introduce-text {
  font-family: "Hanna";
  flex: 1;
}

.introduce-img-container {
  max-width: 50%;
}

.introduce-name {
  font-family: "Hanna";
  font-size: 4em;
  font-weight: bold;
  margin-right: 0px; /* 좀 더 왼쪽으로 이동 */
}

.introduce-contact-info {
  font-family: "Hanna";
  margin-top: 30px;
  font-size: 1.2em;
  border: none;
  
}

.introduce-contact-info strong {
  font-weight: bold;
}

.introduce-text button {
  font-family: "Hanna";
  /* background-color: rgb(192, 192, 192); */
    /* background-color: rgb(245, 245, 245); */
    background-color: rgb(255, 216, 145) ;
  display: block;
  border: none;
  margin-bottom: 30px; /* 버튼 간격을 조정 */
  padding: 15px; /* 버튼 크기를 조정 */
  font-size: 1em; /* 버튼 글자 크기를 조정 */
  transition: background-color 0.3s ease; /* 배경색 전환에 대한 애니메이션 효과 추가 */
  box-sizing: border-box; /* 패딩이나 테두리가 요소의 크기에 영향을 주지 않도록 함 */
  width: 300px;
  height: 60px;
  border-radius: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.introduce-text button:hover {
  background-color: rgb(192, 192, 192); /* 마우스 호버 시 배경색 변경 */
}

.introduce-text button:last-child {
  margin-bottom: 0;
}
